<template>
  <v-container style="max-width: 600px">
    <v-card variant="text">
      <v-card-title>
        Регистрация
      </v-card-title>
      <v-card-text class="mt-6">
        <v-form
            v-model="form"
            @submit.prevent="onSubmit"
        >
          <v-text-field
              v-model="user.fio"
              :rules="[required]"
              class="mb-2"
              clearable
              label="Как вас зовут?"
              variant="outlined"
          />

          <vue-tel-input
              v-model="user.phone"
              style="height: 60px; font-size: 1rem"
              class="mb-6"
          />

          <v-btn
              :disabled="!form"
              :loading="loading"
              type="submit"
              color="success"
              variant="flat"
              rounded block
          >
            Зарегистрироваться
          </v-btn>
        </v-form>
      </v-card-text>
    </v-card>

    <v-snackbar
        v-model="error"
        color="error"
    >
      Ой! Произошла ошибка. Проверьте подключение к интернету
    </v-snackbar>
  </v-container>
</template>

<script>
import olegAPI from "@/api/olegAPI";

export default {
  name: "UserRegister",
  data: () => ({
    user: {
      fio: null,
      phone: null
    },
    loading: false,
    form: false,
    error: false
  }),
  methods: {
    onSubmit () {
      if (!this.form) return
      this.loading = true

      olegAPI.addClient({
        tg_id: this.$store.getters.userTgId,
        name: this.user.fio,
        phone: this.user.phone
      })
          .then(() => {
            this.$router.go({name: 'events'})
          })
          .catch(() => {
            this.error = true
            setTimeout(() => (this.error = false), 10000)
          })
          .finally(() => {
            this.loading = false
          })

    },
    required (v) {
      return !!v || 'Обязательно для заполнения'
    },
  }
}
</script>

