<template>
  <div v-if="error">
    <ApiError :text="error"/>
  </div>
  <div v-else>
    <AccountInfo
        class="mb-8"
        v-if="userData.name"
        :name="userData.name"
        :phone="userData.phone"
    />

    <div class="mt-4 mb-2 text-body-1 font-weight-regular">
      Мои события
    </div>

    <v-tabs
        v-model="selectedEventsTab"
        fixed-tabs hide-slider
    >
      <v-tab value="upcoming">Предстоящие</v-tab>
      <v-tab value="past">Прошедшие</v-tab>
    </v-tabs>

    <v-window v-model="selectedEventsTab">
      <v-window-item value="upcoming">
        <div v-if="userEventsUpcoming.length">
          <v-item-group v-for="event in userEventsUpcoming" :key="event">
            <MyEventInfoCard
                :title="event.name"
                :start="event.start"
                :end="event.end"
                @on-delete-clicked="showUnsubscribeEvent(event)"
            />
          </v-item-group>
        </div>
        <div v-else>
          <NoEvents />
        </div>
      </v-window-item>

      <v-window-item value="past">
        <div v-if="userEventsPast.length">
          <v-item-group v-for="event in userEventsPast" :key="event">
            <MyEventInfoCard
                :title="event.name"
                :start="event.start"
                :end="event.end"
                @on-delete-clicked="showUnsubscribeEvent(event)"
            />
          </v-item-group>
        </div>
        <div v-else>
          <NoEvents />
        </div>
      </v-window-item>
    </v-window>

    <EventUnsubscribeDialog
      :visible="eventUnsubscribeDialog.show"
      @onUnsubscribeClick="(eventId) => unsubscribeEvent(eventId)"
      @close="hideUnsubscribeEvent"
      :event-name="eventUnsubscribeDialog.eventName"
      :event-start="eventUnsubscribeDialog.eventStart"
      :event-end="eventUnsubscribeDialog.eventEnd"
      :event-id="eventUnsubscribeDialog.eventId"
    />
  </div>
</template>

<script>
import AccountInfo from "@/components/utils/AccountInfo";
import ApiError from "@/components/utils/ApiError";
import NoEvents from "@/components/utils/NoEvents";
import MyEventInfoCard from "@/components/utils/MyEventInfoCard";
import EventUnsubscribeDialog from "@/components/utils/EventUnsubscribeDialog";
import olegAPI from "@/api/olegAPI";
import {computed, ref} from "vue"
import { useStore } from 'vuex'

export default {
  name: "MyAccount",
  components: {
    AccountInfo,
    ApiError,
    NoEvents,
    MyEventInfoCard,
    EventUnsubscribeDialog
  },
  data: () => ({
    selectedEventsTab: 'upcoming',
    eventUnsubscribeDialog: {
      show: false,
      eventName: null
    }
  }),
  async setup() {
    const store = useStore()

    const userDataRaw = ref({})
    const userEventsRaw = ref({})
    const error = ref(null)

    try {
      userDataRaw.value = await olegAPI.getClientInfo(store.getters.userTgId)
    } catch (err) {
      error.value = err
    }

    const userData = computed(() => {
      return {
        name: userDataRaw.value.name,
        phone: userDataRaw.value.phone
      }
    })

    try {
      userEventsRaw.value = await olegAPI.getClientEvents({
        tg_id: store.getters.userTgId,
        client_id: null
      })
    } catch (err) {
      error.value = err
    }

    const userEventsUpcoming = computed(() => {
      const eventsRaw = userEventsRaw.value.booking_next
      const events = []
      for (const event of eventsRaw) {
        events.push({
          name: event.subscription_service.name_service,
          start: new Date(Date.parse(event.booking_day_start + ' ' + event.booking_time_start)),
          end: new Date(Date.parse(event.booking_day_end + ' ' + event.booking_time_end)),
          id: event.id_booking
        })
      }
      return events
    })

    const userEventsPast = computed(() => {
      const eventsRaw = userEventsRaw.value.booking_previous
      const events = []
      for (const event of eventsRaw) {
        events.push({
          name: event.subscription_service.name_service,
          start: new Date(event.booking_day_start + ' ' + event.booking_time_start),
          end: new Date(event.booking_day_end + ' ' + event.booking_time_end),
          id: event.id_booking
        })
      }
      return events
    })

    return {
      userData,
      userEventsUpcoming,
      userEventsPast,
      error
    }
  },
  methods: {
    showUnsubscribeEvent(event) {
      this.eventUnsubscribeDialog.show = true
      this.eventUnsubscribeDialog.eventName = event.name
      this.eventUnsubscribeDialog.eventStart = event.start
      this.eventUnsubscribeDialog.eventEnd = event.end
      this.eventUnsubscribeDialog.eventId = event.id
    },
    hideUnsubscribeEvent() {
      this.eventUnsubscribeDialog.show = false
      this.eventUnsubscribeDialog.eventName = null
      this.eventUnsubscribeDialog.eventStart = null
      this.eventUnsubscribeDialog.eventEnd = null
      this.eventUnsubscribeDialog.eventId = null
    },
    async unsubscribeEvent(event_id) {
      await olegAPI.unsubscribeFromEvent(event_id)
          .then(() => {
            this.hideUnsubscribeEvent()
            this.$router.go(this.$router.currentRoute.path)
          })
          .catch((err) => {
            console.log(err)
          })
    }
  }
}
</script>

