import { createStore } from 'vuex'

export default createStore({
  state() {
    return {
      themeDark: false,
      userData: {
        tg_id: null
      }
    }
  },
  getters: {
    userTgId(state) {
      return state.userData.tg_id
    }
  },
  mutations: {
    setTheme(state, mode) {
      if (mode == 'dark') { state.themeDark = true }
      else { state.themeDark = false }
    },
    setUserData(state, { tg_id }) {
      state.userData.tg_id = tg_id
    }
  },
  actions: {
  },
  modules: {
  }
})
