<template>
  <div>
    <v-date-picker
        :attributes="attributes"
        @dayclick="onDayClick"
        v-model="selectedDate"
        :select-attribute="selectAttribute"
        :available-dates="availableDates"
        :disabled-dates="{}"
        is-expanded locale="ru-RU" :is-dark="$store.state.themeDarkMode" style="border: none"
    />
  </div>
</template>

<script>

export default {
  name: "EventCalendar",
  data() {
    return {
      selectedDate: null,
      selectAttribute: {
        highlight: {
          fillMode: 'solid',
          color: 'blue'
        }
      }
    }
  },
  props: [
    'dates'
  ],
  methods: {
    onDayClick(date) {
      if (this.selectedDate != null) {
        this.$emit('dayclick', date.id)
      }
      else {
        this.$emit('dayclick', null)
      }
    }
  },
  computed: {
    attributes() {
      return [
        {
          dates: this.dates.map(date => date.date)
        }
      ]
    },
    availableDates() {
      return this.dates.map(date => ({
        start: date.date,
        end: date.date
      }))
    }
  }
}
</script>

