<template>
  <v-dialog v-model="show" persistent max-width="600px">
    <v-card>
      <v-card-title>
        Отменить запись?
      </v-card-title>

      <v-card-text>
        <div class="mb-1">
          {{ eventName }}
        </div>

        <div class="text-body-2 text-medium-emphasis">
          <div v-if="timeIntervalFormatted.same_day">
            <IconAndText icon="mdi-clock-outline" :text="timeIntervalFormatted.start_time + ' - ' + timeIntervalFormatted.end_time"/>
            <IconAndText icon="mdi-calendar" :text="timeIntervalFormatted.date"/>
          </div>

          <div v-else>
            <IconAndText icon="mdi-calendar" :text="'c ' + timeIntervalFormatted.start + ' по ' + timeIntervalFormatted.end"/>
          </div>
        </div>
      </v-card-text>

      <v-card-actions class="justify-end">
        <v-btn color="error" flat @click="$emit('onUnsubscribeClick', this.eventId)">Да, отписаться</v-btn>
        <v-btn color="primary" flat @click.stop="show=false">Нет</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import IconAndText from "@/components/utils/IconAndText";
import moment from "moment";

export default {
  name: "EventUnsubscribeDialog",
  components: {
    IconAndText
  },
  props: [
      'visible',
      'eventName',
      'eventStart',
      'eventEnd',
      'eventId'
  ],
  computed: {
    show: {
      get () {
        return this.visible
      },
      set (value) {
        if (!value) {
          this.$emit('close')
        }
      }
    },
    timeIntervalFormatted: function () {
      const start_date = moment(this.start).format("YYYY-MM-DD")
      const end_date = moment(this.end).format("YYYY-MM-DD")

      if (start_date === end_date) {
        return {
          "same_day": true,
          "start_time": moment(start_date).locale('ru').format('LT'),
          "end_time": moment(end_date).locale('ru').format('LT'),
          "date": moment(start_date).locale('ru').format('LL')
        }
      }
      else {
        return {
          "same_day": false,
          "start": moment(start_date).locale('ru').format('LLL'),
          "end": moment(end_date).locale('ru').format('LLL'),
        }
      }
    }
  }
}
</script>