<template>
  <div v-if="error">
    <ApiError :text="error"/>
  </div>
  <div v-else>
    <div v-if="events.length">
      <div v-for="event in events" :key="event">
        <!--
        <v-autocomplete
          variant="underlined"
          color="primary"
          placeholder="Услуга или событие"
          append-inner-icon="mdi-magnify"
          :items="events"
          item-title="name_service"
          item-value="id"
        />
        {{events}}-->
        <router-link :to="{ name: 'event', params: { eventId: event.id }}" style="text-decoration: none">
          <EventInfoCard
            :title="event.name_service"
            :duration="event.duration"
            :max-booking="event.max_booking"
            :price="event.price_service"
          />
        </router-link>

      </div>
    </div>
    <div v-else>
      <v-alert
          color="gray"
          icon="mdi-eye-outline"
          title="Нет событий"
          variant="text"
      >
      </v-alert>
    </div>
  </div>
</template>

<script>
import EventInfoCard from "@/components/utils/EventInfoCard";
import olegAPI from "@/api/olegAPI";
import ApiError from "@/components/utils/ApiError";
import { ref } from "vue";

export default {
  name: "EventSearch",
  components: {
    EventInfoCard,
    ApiError
  },

  async setup() {
    const events = ref([])
    const error = ref(null)

    try {
      events.value = await olegAPI.getAllEvents()
    }
    catch (err) {
      error.value = err
    }

    return {
      events,
      error
    }
  }
}
</script>

