<template>
  <v-hover>
    <template v-slot:default="{ isHovering, props }">
      <v-card
          class="rounded-xl mt-3 mb-3" variant="tonal" color="primary"
          :class="isHovering ? 'm3-border-black' : 'm3-border-transparent'"
          style="transition: .2s ease-in-out;"

          v-bind="props"
      >
        <div class="d-flex align-center">
          <div class="d-flex flex-column flex-grow-1">
            <div class="d-flex flex-nowrap align-top justify-space-between">
              <div class="d-flex flex-column">
                <div class="ma-2 ml-4 text-h6 font-weight-regular text-high-emphasis">
                  {{ title }}
                </div>
              </div>

              <div class="mr-4 mt-3 ma-2 text-subtitle-1">

              </div>
            </div>

            <div class="d-flex ml-4 mb-3 ml-2 text-body-2">
              <!--<IconAndText icon="mdi-clock-outline" :text="timeIntervalFormatted"/>-->
              <div v-if="timeIntervalFormatted.same_day">
                <IconAndText icon="mdi-clock-outline" :text="timeIntervalFormatted.start_time + ' - ' + timeIntervalFormatted.end_time"/>
                <IconAndText icon="mdi-calendar" :text="timeIntervalFormatted.date"/>
              </div>
              <div v-else>
                <IconAndText icon="mdi-calendar" :text="'c ' + timeIntervalFormatted.start + ' по ' + timeIntervalFormatted.end"/>
              </div>
            </div>
          </div>

          <div class="ma-2">
            <v-avatar>
              <v-btn
                @click="onDeleteClicked"
                icon="mdi-close"
                variant="text"
                color="error"
                size="small"
              />
            </v-avatar>
          </div>
        </div>
      </v-card>
    </template>
  </v-hover>
</template>c

<script>
import IconAndText from "@/components/utils/IconAndText";
import moment from "moment/moment"

export default {
  name: "MyEventInfoCard",
  props: [
    'title',
    'start',
    'end'
  ],
  components: {
    IconAndText
  },
  computed: {
    /*
    priceFormatted: function () {
      return new Intl.NumberFormat('ru-RU', { style: 'currency', currency: 'RUB' }).format(this.price)
    },
    durationFormatted: function () {
      return moment.duration(this.duration).locale("ru").humanize();
    },

     */
    timeIntervalFormatted: function () {
      const start_datetime = this.start
      const end_datetime = this.end

      if (moment(start_datetime).format("YYYY-MM-DD") === moment(end_datetime).format("YYYY-MM-DD")) {
        return {
          "same_day": true,
          "start_time": moment(start_datetime).locale('ru').format('LT'),
          "end_time": moment(end_datetime).locale('ru').format('LT'),
          "date": moment(start_datetime).locale('ru').format('LL')
        }
      }
      else {
        return {
          "same_day": false,
          "start": moment(start_datetime).locale('ru').format('LLL'),
          "end": moment(end_datetime).locale('ru').format('LLL'),
        }
      }
    }
  },
  methods: {
    onDeleteClicked() {
      this.$emit('onDeleteClicked')
    }
  }
}
</script>

