import axios from "axios";

export const olegApiInstance = () => axios.create({
  baseURL: 'https://olegbackend.ru/api_booking', // 'http://192.168.3.142:5000/api_booking',// 'https://olegbackend.ru/api_booking',
  headers: {
    common: {
      'X-API-KEY': 'oleg'
    },
    post: {
      'Content-type': 'application/x-www-form-urlencoded'
    }
  },
  timeout: 10000
})

const urls = {
  events_all: '/servicetg',
  event_info: '/booking/search',
  event_subscribe: '/booking',
  event_unsubscribe: '/booking',
  client_info: '/client/info_client',
  client_events: '/booking/my_booking',
  client_check_tg: '/client/info_client',
  client_add: '/client',
}

export const defaultUserParams = {
  tg_id: 1
}

export default {
  getAllEvents() {
    return olegApiInstance().get(urls.events_all)
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  getEventData(service_id) {
    return olegApiInstance().get(urls.event_info, {
      params: {
        "id_service": service_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  subscribeToEvent({service_id, tg_id, day_id, day_start, day_end, time_start, time_end, creator_event_id}) {
    return olegApiInstance().post(urls.event_subscribe, {
      "booking_time_start": time_start,
      "booking_time_end": time_end,
      "event_day_id": day_id,
      "booking_day_start": day_start,
      "booking_day_end": day_end,
      "service_id": service_id,
      // "staff_id": staff_id,
      "client_info": {
        "tg_id": tg_id,
        "client_id": null
      },
      "creator_event_id": creator_event_id
    })
  },
  unsubscribeFromEvent(event_id) {
    return olegApiInstance().delete(urls.event_unsubscribe, {
      params: {
        id_bookings: event_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  getClientInfo(tg_id) {
    return olegApiInstance().get(urls.client_info, {
      params: {
        tg_id: tg_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  getClientEvents({ tg_id, client_id }) {
    return olegApiInstance().get(urls.client_events, {
      params: {
        id_tg: tg_id,
        id_client: client_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  checkClientTg(tg_id) {
    return olegApiInstance().get(urls.client_check_tg, {
      params: {
        tg_id: tg_id
      }
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  },
  addClient({tg_id, name, phone}) {
    return olegApiInstance().post(urls.client_add, {
      "tg_id": tg_id,
      "name": name,
      "phone": phone
    })
      .then((resp) => {
        return resp.data
      })
      .catch((e) => {
        throw e
      })
  }
}