<template>
  <v-chip-group v-model="selectedTime" color="primary" column>
    <v-chip
      v-for="time in times.times"
      :key="time"
      variant="text" rounded
      @click="onTimeSelect"
    >
      {{ time[0] }} - {{ time[1] }}
    </v-chip>
  </v-chip-group>
</template>

<script>
export default {
  name: "EventTimes",
  props: [
    'times'
  ],
  data: () => ({
    selectedTime: null
  }),
  methods: {
    onTimeSelect () {
      if (this.times.times[this.selectedTime] !== undefined) {
        this.$emit('timeclick', this.times.times[this.selectedTime])
      }
      else {
        this.$emit('timeclick', null)
      }
    }
  },
  watch: {
    times: function () {
      this.selectedTime = null
    }
  }
}
</script>

