<template>
  <v-app id="app">
    <v-app-bar v-if="isTelegram && isRegistered" density="compact">
      <v-spacer/>
      <v-tabs hide-slider>
        <v-tab :to="{name: 'events'}" class="text-subtitle-1">
          События
        </v-tab>
        <v-tab :to="{name: 'my_events'}" class="text-subtitle-1">
          Мои записи
        </v-tab>
      </v-tabs>
      <v-spacer/>
    </v-app-bar>

    <v-main v-if="isTelegram">
      <!--<code>{{ $store.state.userData }}</code>-->
      <!--
      <router-view v-if="isTelegram" v-slot="{ Component, route }">
        <transition name="slide-x" mode="out-in">
          <component :is="Component" :key="route.path"/>
        </transition>
      </router-view>
      -->

      <router-view v-if="isRegistered"/>

      <UserRegister v-else/>
    </v-main>

    <v-main v-else>
      <NotInTelegram class="mt-12" @onSetDefaultUserClick="setTestTelegramUserId"/>
    </v-main>

    <AppFooter/>
  </v-app>
</template>

<script>
import AppFooter from "@/components/AppFooter";
import NotInTelegram from "@/components/utils/NotInTelegram";
import UserRegister from "@/components/utils/UserRegister";
import olegAPI from "@/api/olegAPI";

window.addEventListener('keypress',
    () => console.log('click')
)

export default {
  name: 'App',
  components: {
    AppFooter,
    NotInTelegram,
    UserRegister
  },
  data: () => ({
    isTelegram: false,
    isRegistered: false,
    loaded: false
  }),

  async mounted() {
    const tg = window.Telegram.WebApp
    await tg.ready()
    // Setting theme based on current Telegram theme
    this.setTheme(tg.colorScheme)
    this.$store.commit('setTheme', tg.colorScheme)

    tg.onEvent('themeChanged', () => (this.setTheme(tg.colorScheme)))

    // Check for Telegram user data
    const tgUserInitData = tg.initDataUnsafe

    // поправить на try catch
    if (tgUserInitData.user.id) {
      this.$store.commit('setUserData', {
        tg_id: tgUserInitData.user.id
      })
      this.isTelegram = true
    } else {
      this.isTelegram = false
    }

    const userCheckData = await olegAPI.checkClientTg(tgUserInitData.user.id)

    if (userCheckData.tg_id !== undefined) {
      this.isRegistered = true
    }

    this.loaded = true
  },
  methods: {
    setTheme(theme) {
      console.log('theme', theme)
      if (theme == 'dark') {
        this.$vuetify.theme.name = 'dark'
        this.$store.commit('setTheme', 'dark')
        console.log('set dark')
      } else {
        this.$vuetify.theme.name = 'light'
        this.$store.commit('setTheme', 'light')
        console.log('set light')
      }
    },
    setTestTelegramUserId(test_tg_id) {
      this.$store.commit('setUserData', {
        tg_id: test_tg_id
      })
      this.isTelegram = true
    }
  }
}
</script>

<style>
#app {
  background-color: rgba(var(--v-theme-background), 0.7) !important;
}

.slide-x-enter-active,
.slide-x-leave-active {
  transition: transform .2s cubic-bezier(0.25, 0.8, 0.5, 1), opacity .2s;
  opacity: 1;
}

.slide-x-enter-from,
.slide-x-leave-to {
  opacity: 0;
}

.slide-x-enter-from {
  transform: translateX(100px);
}

.slide-x-leave-to {
  transform: translateX(-100px);
}
</style>
