<template>
  <div>
    <div class="d-flex justify-space-between align-center">
      <div class="text-h6 font-weight-medium">
        {{ title }}
      </div>
      <div class="text-body-1">
        {{ priceFormatted }}
      </div>
    </div>
    <div class="d-flex text-subtitle-2 text-medium-emphasis mt-1">
      <IconAndText v-if="duration" icon="mdi-clock-outline" :text="durationFormatted"/>
      <IconAndText v-if="maxBooking" icon="mdi-account-group" :value="maxBooking" text="чел"/>
    </div>
  </div>
</template>

<script>
import IconAndText from "@/components/utils/IconAndText";
import moment from "moment";

export default {
  name: "EventInfo",
  props: [
    'title',
    'duration',
    'maxBooking',
    'price'
  ],
  components: {
    IconAndText
  },
  computed: {
    priceFormatted: function () {
      return new Intl.NumberFormat('ru-RU', { style: 'currency', currency: 'RUB' }).format(this.price)
    },
    durationFormatted: function () {
      return moment.duration(this.duration).locale("ru").humanize();
    },
  }
}
</script>

