<template>
  <div v-if="error">
    <ApiError :text="error"/>
  </div>
  <div v-else>
    <!-- Диалог, что успешна/неуспешна запись -->

    <div v-if="eventDialog.show" style="max-width: 600px">
      <v-card>
        <v-toolbar>
          <v-toolbar-title>
            <v-alert
                v-if="eventDialog.success"
                variant="text"
                color="success"
                type="success"
                class="pl-1"
            >
              <v-alert-title>Вы записаны</v-alert-title>
            </v-alert>

            <v-alert
                v-else
                variant="text"
                color="red"
                type="error"
                class="pl-1"
            >
              <v-alert-title>Ошибка</v-alert-title>
            </v-alert>
          </v-toolbar-title>

          <v-btn icon @click="closeEventDialog">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>

        <v-card-text>

        </v-card-text>

        <!-- this.$router.go(this.$router.currentRoute.path) // перезагрузка страницы, в будущем будет обновление данных -->
        <v-card-actions class="justify-end">
          <v-btn v-if="eventDialog.success" color="primary" @click="$router.push({ name: 'events' })">Все события</v-btn>
          <v-btn v-if="eventDialog.success" color="success" @click="closeEventDialog">Записаться еще</v-btn>
          <v-btn v-if="!eventDialog.success" color="red" @click="closeEventDialog">Попробовать снова</v-btn>
        </v-card-actions>
      </v-card>
    </div>

    <!-- Форма записи -->

    <div v-else>
      <!-- Информация о событии -->

      <div class="mt-4 mb-6 ml-4 mr-4">
        <EventInfo
            :title="eventInfo.name"
            :duration="eventInfo.duration"
            :max-booking="eventInfo.max_booking"
            :price="eventInfo.price"
        />
      </div>

      <!-- Запись -->

      <v-card class="rounded-xl" variant="text">

        <v-card-title class="text-primary text-subtitle-1">
          Запись на мероприятие
        </v-card-title>

        <!-- Выбор дня -->

        <v-card-text class="pa-0">
          <EventCalendar :dates="eventDates" @dayclick="onDayClick"/>
        </v-card-text>

        <!-- Выбор времени -->

        <div v-if="selectedDayTimes">

          <v-card-title class="text-medium-emphasis text-subtitle-2">
            Доступное время
          </v-card-title>

          <v-card-text>
            <EventTimes :times="selectedDayTimes" @timeclick="onTimeClick"/>
          </v-card-text>
        </div>

        <!-- Кнопка записи -->

        <v-card-actions v-if="readyToSubscribe">
          <v-btn
              color="success"
              variant="flat"
              rounded block
              @click="subscribeToEvent"
          >
            Записаться
          </v-btn>
        </v-card-actions>
      </v-card>
    </div>
  </div>
</template>

<script>
import EventInfo from "@/components/utils/EventInfo";
import EventCalendar from "@/components/utils/EventCalendar";
import EventTimes from "@/components/utils/EventTimes";
import ApiError from "@/components/utils/ApiError";
import olegAPI from "@/api/olegAPI";
import {useRoute} from "vue-router"
import {ref, computed} from "vue";

export default {
  name: "EventSubscribe",
  components: {
    EventInfo,
    EventCalendar,
    EventTimes,
    ApiError,
  },

  async setup() {
    // for eventId
    const route = useRoute()

    const event = ref({})
    const error = ref(null)

    // задел на будущее: обновление данных о событии после успешной записи
    const getEventInfo = async () => {
      try {
        event.value = await olegAPI.getEventData(route.params.eventId)
      } catch (err) {
        error.value = err
      }
    }

    await getEventInfo()

    const eventInfo = computed(() => {
      const info = event.value.info_service
      return {
        id: info.id,
        name: info.name_service,
        duration: info.duration,
        price: info.price_service,
        max_booking: info.max_booking
      }
    })

    const eventDates = computed(() => {
      let eventDates = []

      for (const eventDay of event.value.window_interval) {
        for (const eventDayTime of eventDay.intervals) {
          eventDates.push({
            date: Date.parse(eventDay.day + " " + eventDayTime[0]),
          })
        }
      }

      return eventDates
    })

    // косяк Ильи с ключами
    const selectedDayTimes = ref(null)

    const selectedEvent = ref({
      date: null,
      time: null
    })

    const readyToSubscribe = computed(() => {
      if (selectedEvent.value.date !== null && selectedEvent.value.time !== null) {
        return true
      } else {
        return false
      }
    })

    return {
      event,
      eventInfo,
      getEventInfo,
      eventDates,
      selectedDayTimes,
      selectedEvent,
      readyToSubscribe,
      error
    }
  },

  data() {
    return {
      eventDialog: {
        show: false,
        success: false
      }
    }
  },

  methods: {
    onDayClick(date) {
      this.selectedDayTimes = this.getTimesForClickedDate(date)

      this.selectedEvent.date = date
      this.selectedEvent.time = null
    },
    getTimesForClickedDate(date) {
      for (const day of this.event.window_interval) {
        if (date == day.day) {
          this.selectedEvent.day_id = day.id_event
          this.selectedEvent.creator_event_id = day.user_id
          return {
            day: day.day,
            times: day.intervals,
          }
        }
      }

      return null
    },
    onTimeClick(time) {
      this.selectedEvent.time = time
    },
    async subscribeToEvent() {
      await olegAPI.subscribeToEvent({
        tg_id: this.$store.getters.userTgId,
        day_id: this.selectedEvent.day_id,
        day_start: this.selectedEvent.date,
        day_end: this.selectedEvent.date,
        time_start: this.selectedEvent.time[0],
        time_end: this.selectedEvent.time[1],
        service_id: this.eventInfo.id,
        creator_event_id: this.selectedEvent.creator_event_id
      })
          .then(() => {
            this.eventDialog.show = true
            this.eventDialog.success = true
          })
          .catch(() => {
            this.eventDialog.show = true
            this.eventDialog.success = false
          })
    },
    closeEventDialog() {
      this.eventDialog.show = false
      if (this.eventDialog.success) {
        this.$router.go(this.$router.currentRoute.path)
      }
    }
  }
}
</script>

